body {
  background-repeat: no-repeat;
  background-size: cover;
}

.link-container .link {
  color: #000;
  font-size: 18px;
  text-decoration: none;
  transition: transform .3s ease-in-out;
}

.calendar {
  justify-content: center;
  align-items: center;
  gap: 60px;
  display: flex;
}

.date-style {
  color: #fff;
  background-color: #789;
  border-radius: 4px;
  gap: 15px;
  padding: 10px;
  font-size: 36px;
  display: flex;
}

.digital-clock {
  text-align: center;
  width: 200px;
  color: #fff;
  height: 50px;
  background-color: #789;
  border-radius: 4px;
  margin: 50px auto 100px;
  padding-top: 10px;
  font-size: 36px;
}

.clock {
  width: 290px;
  height: 290px;
  background-color: rgba(132, 129, 129, 0);
  background-image: url("pi5oLon5T.bd20f39a.png");
  background-repeat: no-repeat;
  background-size: cover;
  margin-bottom: 100px;
  position: relative;
  left: 40%;
}

.clock-arrow {
  transform-origin: bottom;
  background-color: #000;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  position: absolute;
  transform: translate(-50%);
}

.clock-center {
  width: 16px;
  height: 16px;
  background-color: #000;
  border-radius: 50%;
  position: absolute;
  top: 46%;
  left: 48%;
}

.clock-seconds__arrow {
  width: 2px;
  height: 48%;
  bottom: 50%;
  left: 50%;
}

.clock-minutes__arrow {
  width: 5px;
  height: 38%;
  transition: all .3s ease-in-out;
  bottom: 50%;
  left: 50%;
}

.clock-hours__arrow {
  width: 7px;
  height: 30%;
  transition: all .3s ease-in-out;
  bottom: 50%;
  left: 50%;
}

